import React, { Component } from "react";
class Session35 extends Component{
    constructor(){
        super();
        this.state = {
            name: "",
            email: "",
            phone: 0,
            sport: ""
        }
    }
    render(){
        return(
            <>
           
            <form>
                <label>Name:</label>
                <input className="form-control" type="text" value={this.state.name} 
                onChange={(e)=>this.setState({name: e.target.value})}/><br/>
                <label>Email:</label>
                <input className="form-control" type="email" value={this.state.email} 
                onChange={(e)=>this.setState({email: e.target.value})}/><br/>
                <label>Phone:</label>
                <input className="form-control" type="number" value={this.state.phone} 
                onChange={(e)=>this.setState({phone:e.target.value})}/><br/>
                <label>Please select sport:</label>
                <select className="form-control" value={this.state.sport} 
                onChange={(e)=>this.setState({sport: e.target.value})}>
                    <option value="cricket">Cricket</option>
                    <option value="football">Football</option>
                    <option value="basketball">Basketball</option>
                </select>
            </form>
            <h2>User Details:</h2>
            <h3>Name:{this.state.name}</h3>
            <h3>Email:{this.state.email}</h3>
            <h3>Phone:{this.state.phone}</h3>
            <h3>Sport:{this.state.sport}</h3>
            </>
        )
    }
}
export default Session35;